.footerContainer{
    background-image: url(../img/healthy-diet-background.png);
    height: 450px;
    justify-content: space-around;
    position: relative;
    p{
        cursor: pointer;
        transition: all 0.2s;
        &:hover{
            transform: translateX(5px);
            transition: all 0.2s;
        }
    }
    @media only screen and (max-width: 670px) {
        flex-direction: column;
       text-align: center;
       height: 100%;
    }
}

.googleMap{
    margin-top: 10px;
    iframe{
        @media only screen and (max-width: 850px) {
            height: 270px;
            width: 300px;
        }
        @media only screen and (max-width: 320px) {
            height: 240px;
            width: 280px;
        }
    }
    
}

.adresse{
    .logo{
        height: 40px;
        margin: 10px;
    }
}

.adresseCuisine{
    cursor: pointer;
    @media only screen and (max-width: 670px) {
        margin-bottom: 100px;
    }
}

.signature{
    position: absolute;
    right: 0px;
    bottom: 0px;
    font-size: 13px;
    margin: 10px;
    cursor: pointer;
    span{
        font-weight: bold;
        color: orange;
        border: 3px solid orange;
    }

    @media only screen and (max-width: 850px) {
        left: 0;
    }
}