.presContainer{
    background-image: url(../img/white-concrete-wall.png);
    background-size: cover;
    height:900px;
}
.photos{
    h2{
        text-align: center;
    }
}
.eachViews{
   display: flex;
   justify-content: center;
   margin: 0 auto;
   
   img{
    margin-top: 20px;
    height: 500px;
    margin: 5px;
    border-radius: 10px;
    opacity: 0.9;
    position: relative;
    background: white;
    border-radius: 0% 0% 0% 0% / 0% 0% 0% 0% ;
    color: white;
    box-shadow: 20px 20px rgba(0,0,0,.15);
    transition: all .4s ease;
    @media only screen and (max-width: 1000px) {
        height: 400px;
    }
    @media only screen and (max-width: 800px) {
        height: 300px;
    }
    @media only screen and (max-width: 550px) {
        height: 220px;
    }
    @media only screen and (max-width: 350px) {
        height: 150px;
    }
    &:hover{
        border-radius: 0% 0% 50% 50% / 0% 0% 5% 5% ;
        box-shadow: 10px 10px rgba(0,0,0,.25);
    }
   }
}

.texte{
    margin-top: 20px;
    width: 500px;
    text-align: center;
    background: white;
    padding: 10px;
    border-radius: 10px;
    box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset, rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    font-style: italic;
    @media only screen and (max-width: 1000px) {
        font-size: 0.9em;
    }
    @media only screen and (max-width: 800px) {
        font-size: 0.8em;
        width: 400px;
    }
    @media only screen and (max-width: 550px) {
        font-size: 0.8em;
        width: 300px;
    }
    @media only screen and (max-width: 350px) {
        font-size: 0.7em;
        width: 250px;
    }
}